.subtotal{
    display:flex;
    flex-direction:column;
    width:300px;
    height:100px;
    padding:20px;
    border:1px solid #ddd;
    border-radius:3px;
    background-color:#f3f3f3;
}
.subtotal_gift{
    display:flex;
    align-items:center;
}
.subtotal_gift input{
    margin-right:5px;
}
.subtotal button{
    width:100%;
    height:30px;
    margin-top:15px;
    color:#111;
    border:1px solid #222;
    border-radius:2px;
    background-color:#f0c14b;
}